const airlines = require('./data/airlines.json');
const airports = require('./data/airports.json');
const countries = require('./data/countries.json');
const planes = require('./data/planes.json');

module.exports = {
  getAllData: () => {
    return { airlines, airports, countries, planes };
  },
  getAirlineBy(prop, value) {
    return airlines.filter(airline => airline[prop] == value)[0];
  },
  getAllAirlinesBy(prop, value) {
    return airlines.filter(airline => airline[prop] == value);
  },
  getAirportBy(prop, value) {
    return airports.filter(airport => airport[prop] == value)[0];
  },
  getAllAirportsBy(prop, value) {
    return airports.filter(airport => airport[prop] == value);
  },
  getCountryBy(prop, value) {
    return countries.filter(country => country[prop] == value)[0];
  },
  getPlaneBy(prop, value) {
    return planes.filter(plane => plane[prop] == value)[0];
  }
}